'use client';

import React from 'react';
import Template from './Template';
import {
  Box,
  Typography,
  Grid,
  Card,
  CardContent,
  Button,
  Avatar,
  Divider,
} from '@mui/material';
import {
  Warning,
  Settings,
  LocationCity,
  Terminal,
} from '@mui/icons-material';
import { useRouter } from 'next/navigation';

const primaryColor = '#00ED64';       // MongoDB Green
const secondaryColor = '#0A0F19';     // MongoDB Background

export default function DashboardPage() {
  const router = useRouter();

  const statistics = [
    { title: 'Total Stations', count: 42, icon: <LocationCity /> },
    { title: 'Devices Connected', count: 125, icon: <Terminal /> },
    { title: 'Active Alerts', count: 3, icon: <Warning /> },
    { title: 'Configurations', count: 12, icon: <Settings /> },
  ];

  const quickActions = [
    {
      title: 'Manage Station Info',
      desc: 'Update and view station metadata and parameters.',
      btn: 'Open Station Info',
      path: '/rms/station-info',
    },
    {
      title: 'Platform & Devices',
      desc: 'Manage connected platforms and edge devices.',
      btn: 'Manage Devices',
      path: '/rms/platforms-devices',
    },
    {
      title: 'CAP Alerts',
      desc: 'Monitor and configure Common Alert Protocol alerts.',
      btn: 'Go to Alerts',
      path: '/rms/cap-alerts',
    },
    {
      title: 'Event Logs',
      desc: 'View detailed logs of system activity.',
      btn: 'View Logs',
      path: '/rms/event-logs',
    },
    {
      title: 'RMS Settings',
      desc: 'Manage system configurations and thresholds.',
      btn: 'Open Settings',
      path: '/rms/settings',
    },
    {
      title: 'User Management',
      desc: 'Admin-level access to add, remove, or manage users.',
      btn: 'Manage Users',
      path: '/admin/users',
    },
  ];

  return (
    <Template>
      <Box
        sx={{
          flexGrow: 1,
          display: 'flex',
          flexDirection: 'column',
          minHeight: '100vh',
          backgroundColor: secondaryColor,
          color: 'white',
          px: 4,
          py: 3,
        }}
      >
        <Typography variant="h4" gutterBottom sx={{ color: primaryColor, fontWeight: 600 }}>
          Railway Management System Dashboard
        </Typography>

        <Typography variant="body1" sx={{ mb: 3 }}>
          Welcome to the centralized RMS Admin Panel. Here you can oversee stations,
          devices, alerts, and platform configurations.
        </Typography>

        <Grid container spacing={3}>
          {statistics.map((item, index) => (
            <Grid item xs={12} sm={6} md={3} key={index}>
              <Card
                sx={{
                  backgroundColor: '#1e272e',
                  borderRadius: 3,
                  boxShadow: 3,
                  color: 'white',
                }}
              >
                <CardContent>
                  <Box display="flex" alignItems="center" gap={2}>
                    <Avatar sx={{ bgcolor: primaryColor }}>{item.icon}</Avatar>
                    <Box>
                      <Typography variant="h6">{item.title}</Typography>
                      <Typography variant="body2">{item.count}</Typography>
                    </Box>
                  </Box>
                </CardContent>
              </Card>
            </Grid>
          ))}
        </Grid>

        <Divider sx={{ my: 5, borderColor: '#333' }} />

        <Typography variant="h5" sx={{ mb: 2, color: primaryColor, fontWeight: 600 }}>
          Quick Actions
        </Typography>

        <Grid container spacing={3}>
          {quickActions.map((action, idx) => (
            <Grid item xs={12} sm={6} md={4} key={idx}>
              <Card
                sx={{
                  backgroundColor: '#2d3436',
                  color: 'white',
                  borderRadius: 3,
                  p: 2,
                  boxShadow: 4,
                  height: '100%',
                  display: 'flex',
                  flexDirection: 'column',
                  justifyContent: 'space-between',
                }}
              >
                <Box>
                  <Typography variant="h6" gutterBottom>
                    {action.title}
                  </Typography>
                  <Typography variant="body2" sx={{ mb: 2 }}>
                    {action.desc}
                  </Typography>
                </Box>
                <Button
                  variant="contained"
                  sx={{
                    bgcolor: primaryColor,
                    color: 'black',
                    mt: 'auto',
                    fontWeight: 600,
                    textTransform: 'none',
                  }}
                  onClick={() => router.push(action.path)}
                >
                  {action.btn}
                </Button>
              </Card>
            </Grid>
          ))}
        </Grid>

        <Box sx={{ mt: 6, textAlign: 'center', opacity: 0.6 }}>
          <Typography variant="body2">
            © {new Date().getFullYear()} RMS Admin Panel • Built with MongoDB theme
          </Typography>
        </Box>
      </Box>
    </Template>
  );
}
