'use client';

import React, { useState } from 'react';
import PropTypes from 'prop-types';
import {
  AppBar,
  Box,
  Toolbar,
  Typography,
  IconButton,
  Avatar,
  Menu,
  MenuItem,
  Divider,
  Tooltip,
  useTheme,
  useMediaQuery,
} from '@mui/material';
import { styled } from '@mui/material/styles';
import MenuIcon from '@mui/icons-material/Menu';
import Logout from '@mui/icons-material/Logout';
import Settings from '@mui/icons-material/Settings';
import AccountCircle from '@mui/icons-material/AccountCircle';
import { useUser } from '@/contexts/UserContext';
import { useRouter } from 'next/navigation';

interface AppToolbarProps {
  handleDrawerToggle: () => void;
}

const themeConfig = { primaryColor: '#00ED64', secondaryColor: '#0A0F19' };

const StyledAppBar = styled(AppBar)(({ theme }) => ({
  background: `linear-gradient(90deg, ${themeConfig.primaryColor}cc, ${themeConfig.primaryColor}ee)`,
  boxShadow: `0 4px 12px ${themeConfig.primaryColor}33`,
  zIndex: theme.zIndex.drawer + 1,
  width: { sm: `calc(100% - 240px)` },
  marginLeft: { sm: '240px' },
}));

const StyledMenu = styled(Menu)(({ theme }) => ({
  '& .MuiPaper-root': {
    backgroundColor: '#f8f9fa',
    color: themeConfig.secondaryColor,
    borderRadius: '12px',
    boxShadow: `0 4px 12px ${themeConfig.primaryColor}44`,
    marginTop: '8px',
    '& .MuiMenuItem-root': {
      fontFamily: "'Inter', sans-serif",
      fontSize: '0.9rem',
      padding: '8px 16px',
      '&:hover': {
        backgroundColor: `${themeConfig.primaryColor}11`,
        color: themeConfig.primaryColor,
      },
    },
    '&:before': {
      content: '""',
      position: 'absolute',
      top: 0,
      right: 14,
      width: 10,
      height: 10,
      backgroundColor: '#f8f9fa',
      transform: 'translateY(-50%) rotate(45deg)',
      zIndex: 0,
    },
  },
}));

const StyledIconButton = styled(IconButton)(({ theme }) => ({
  color: themeConfig.secondaryColor,
  '&:hover': {
    backgroundColor: `${themeConfig.primaryColor}33`,
  },
}));

const AppToolbar: React.FC<AppToolbarProps> = ({ handleDrawerToggle }) => {
  const { user, setUser } = useUser();
  const [anchorEl, setAnchorEl] = useState<null | HTMLElement>(null);
  const theme = useTheme();
  const isMobile = useMediaQuery(theme.breakpoints.down('sm'));
  const router = useRouter();

  const handleMenuOpen = (event: React.MouseEvent<HTMLElement>) => {
    setAnchorEl(event.currentTarget);
  };

  const handleMenuClose = () => {
    setAnchorEl(null);
  };

  const handleLogout = () => {
    localStorage.removeItem('token');
    setUser(null);
    router.push('/login');
    handleMenuClose();
  };

  const handleProfile = () => {
    router.push('/dashboard');
    handleMenuClose();
  };

  const handleSettings = () => {
    router.push('/dashboard/settings');
    handleMenuClose();
  };

  return (
    <StyledAppBar position="fixed">
      <Toolbar sx={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center', py: 1 }}>
        <Box sx={{ display: 'flex', alignItems: 'center', gap: 2 }}>
          <Tooltip title={isMobile ? 'Toggle Sidebar' : 'Open/Close Sidebar'}>
            <StyledIconButton
              edge="start"
              onClick={handleDrawerToggle}
              sx={{
                mr: 2,
                ...(isMobile && { fontSize: '1.5rem' }), // Larger icon on mobile
              }}
            >
              <MenuIcon />
            </StyledIconButton>
          </Tooltip>
          <Box sx={{ display: 'flex', alignItems: 'center', gap: 1 }}>
            <Box sx={{ width: 36, height: 36, background: 'white', borderRadius: '8px', p: 0.5, boxShadow: `0 2px 4px ${themeConfig.primaryColor}22` }}>
              <img src="/logo.png" alt="IPIPS Logo" style={{ width: '100%', height: '100%', objectFit: 'contain' }} />
            </Box>
            <Box>
              <Typography
                variant="h6"
                sx={{
                  fontFamily: "'Poppins', sans-serif",
                  fontWeight: 700,
                  color: themeConfig.secondaryColor,
                  fontSize: { xs: '1rem', sm: '1.25rem' },
                }}
              >
                IPIPS - RMS
              </Typography>
              <Typography
                variant="caption"
                sx={{
                  fontFamily: "'Inter', sans-serif",
                  color: themeConfig.secondaryColor,
                  opacity: 0.7,
                  fontSize: '0.7rem',
                }}
              >
                Passenger Information System
              </Typography>
            </Box>
          </Box>
        </Box>
        <Box sx={{ display: 'flex', alignItems: 'center' }}>
          <Tooltip title="Profile Options">
            <StyledIconButton onClick={handleMenuOpen}>
              <Avatar
                sx={{
                  width: 32,
                  height: 32,
                  backgroundColor: `${themeConfig.primaryColor}cc`,
                  color: themeConfig.secondaryColor,
                  fontFamily: "'Inter', sans-serif",
                  fontWeight: 600,
                  fontSize: '0.9rem',
                }}
                src={user?.profilePicture}
              >
                {user?.fullName ? user.fullName.charAt(0).toUpperCase() : <AccountCircle />}
              </Avatar>
            </StyledIconButton>
          </Tooltip>
          <StyledMenu
            anchorEl={anchorEl}
            open={Boolean(anchorEl)}
            onClose={handleMenuClose}
            transformOrigin={{ horizontal: 'right', vertical: 'top' }}
            anchorOrigin={{ horizontal: 'right', vertical: 'bottom' }}
          >
            <MenuItem onClick={handleProfile}>
              <AccountCircle sx={{ mr: 1, color: themeConfig.primaryColor }} />
              Profile
            </MenuItem>
            <MenuItem onClick={handleSettings}>
              <Settings sx={{ mr: 1, color: themeConfig.primaryColor }} />
              Settings
            </MenuItem>
            <Divider sx={{ borderColor: `${themeConfig.primaryColor}33` }} />
            <MenuItem onClick={handleLogout}>
              <Logout sx={{ mr: 1, color: themeConfig.primaryColor }} />
              Logout
            </MenuItem>
          </StyledMenu>
        </Box>
      </Toolbar>
    </StyledAppBar>
  );
};

AppToolbar.propTypes = {
  handleDrawerToggle: PropTypes.func.isRequired,
};

export default AppToolbar;